// Type your data here, it can be string, or any type of data, just write your variable
import {
  SET_DATA_TERIMA_GUDANG_QC_SUCCESS,
  SET_DATA_TERIMA_GUDANG_QC_FAILED,
} from "../actions/terimagudangqc";
const initialState = {
  feedback: [],
  error: null,
  isEdit: false,
};

const terimagudangqc = (state = initialState, action) => {
  switch (action.type) {
    case SET_DATA_TERIMA_GUDANG_QC_SUCCESS:
      return {
        ...state,
        feedback: action.payload.data,
      };
    case SET_DATA_TERIMA_GUDANG_QC_FAILED:
      return {
        ...state,
        error: action.payload.data,
      };
    default:
      return state;
  }
};

export default terimagudangqc;
